<template>
  <div v-once>
    <div id="loader" v-once class="bg-primary"></div>
  </div>
  <HeaderComponent v-once/>
  <div>
    <div class="my-5 py-4"></div>
    <component :is="currentView"/>
  <CookieConcentComponent v-once/>
  </div>
  <FooterComponent v-once/>
  
  <LoginComponent v-if="false" />
</template>

<script>
import 'bootstrap';
import { defineComponent, ref,computed } from 'vue';
import HomeView from "./views/HomeView.vue";
import LoginComponent from "./components/LoginComponent.vue";
import HeaderComponent from "./components/HeaderComponent.vue";
import HelloWorld from './components/HelloWorld.vue';
import FooterComponent from './components/FooterComponent.vue';

import CookieConcentComponent from './components/CookieConcentComponent.vue';
import TreinamentoView from './views/TreinamentoView.vue';
import PostView from './views/PostView.vue';
import CTAComponent from './components/CTAComponent.vue';









export default defineComponent({
  name: 'App',
  components: {
    LoginComponent,
    HeaderComponent,
    FooterComponent,
    CookieConcentComponent,
    
},
  setup() {

    const routes = {
      '/': HomeView,
      '/hello': HelloWorld,
      '/treinamentos': TreinamentoView,
      '/posts': PostView,
      '/cta': CTAComponent
    }

    const currentPath = ref(window.location.hash)

    window.addEventListener('hashchange', () => {
      currentPath.value = window.location.hash
    })

    const currentView = computed(() => {
	console.log((currentPath.value.slice(1)).split('?')[1]	)
      return routes[(currentPath.value.slice(1)).split('?')[0] || '/'] || HomeView;
    })

    return {
      currentView
    }
  }
});





</script>

