  --bs-warning:#fef445 !important;
  --bs-warning-rgb:254, 244, 69 !important;
}



.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

header {
  position: sticky;
  top:0px;
  z-index: 99999;
}

#loader{
  position:fixed;
  top:0px;
  left:0px;
  width:100%;
  height:100vh;
  animation: hide 1.8s forwards;
  z-index: 999999;
}




@keyframes hide {
  0%{
    opacity:1;
  }
  
  100%{
    opacity: 0;
    display: none;
  }
}
</style>
