<script>

import { computed, defineComponent, ref } from "vue";
import UserIcon from "../assets/icons/UserIcon.vue";

export default defineComponent({
    name: "HeaderComponent",

    setup() {
        const headerStick = () => {
            window.onscroll = function () { myFunction() };

            var header = document.querySelector('header')
            var sticky = header.offsetTop;

            function myFunction() {
                if (window.scrollY > sticky) {
                    header.classList.add("sticky");
                } else {
                    header.classList.remove("sticky");
                }
            }
        }
        let getUserPicture = computed(() => {
            let response = !true;
            return response ? { 'img_url': "https://media-gru1-2.cdn.whatsapp.net/v/t61.24694-24/428110468_3729737644020747_2964105524888209857_n.jpg?ccb=11-4&oh=01_ASC59GUSDkn-xS1e20xFwy8WjjCKB01YfdyWo5tp0Kaxig&oe=661019ED&_nc_sid=e6ed6c&_nc_cat=103" } : null;
        })


        let userIcon = getUserPicture;

        const menuLinks = [
            { url: "#/treinamentos", text: "treinamentos" },
            { url: "#/quem-somos", text: "quem somos" },
            { url: "#/blog", text: "blog" },
            { url: "#/contato", text: "contato" },
        ];
        let showMenu = ref(false);
        let goTo = (e)=>{
                let a= document.createElement('a')
                a.href=e
                a.click()
            }
        return {
            menuLinks,
            showMenu,
            userIcon,
            headerStick,
            goTo
        };
    },
    components: { UserIcon }
})
</script>

<template>
    <header @load="headerStick()" class=" px-2 col-12 py-4 bg-primary d-flex flex-row justify-content-center">
        <div class="d-flex flex-sm-row px-sm-5 flex-column justify-content-between col-sm-12 col-12">
            <div class="d-flex justify-content-between">
                <div class=" text-start col-sm-3 text-light " style="cursor:pointer;" @click="goTo('#/')"> <span class=" fw-bold fs-1">Arquitetando.io</span></div>
                <div class="d-flex flex-row justify-content-end">
                    <li class="col-3 list-group-item me-4 d-sm-none list-style-none border-0">
                        <UserIcon />
                    </li>
                    <i class="d-sm-none bi bi-list text-light fs-1" @click="() => showMenu = !showMenu"></i>
                </div>

            </div>
            <nav class="col-sm-8 col-12 d-flex flex-column ">

                <ul id="nav-menu"
                    class="d-none align-items-center list-group-flush d-sm-flex flex-column flex-sm-row fs-5 fw-bold justify-content-between">
                    <li v-for="link in menuLinks" :key="link"
                        class="list-group-item bg-transparent border-0 text-capitalize"><a
                            class="text-light text-decoration-none" :href="link.url">{{ link.text }}</a></li>
                    <li class="btn fs-5 btn-primary text-capitalize"><a class="text-light fw-bold text-decoration-none"
                            href="#/seja-membro">seja membro</a></li>
                    <li class="btn fs-5 btn-light text-capitalize"><a class="text-primary fw-bold text-decoration-none"
                            href="#/login">login</a></li>

                    <li class="col-1 list-group-item  list-style-none border-0" style="max-width:60px;max-width:60px;">
                        <UserIcon :src="userIcon" />
                    </li>
                </ul>

                <ul id="nav-menu-mobile" :class="{ 'd-flex': showMenu, 'd-none': !showMenu }"
                    class="position-fixed top-5 mt-4 col-12 start-0 align-items-center list-group  d-sm-none flex-column fs-5 fw-bold">
                    <li v-for="link in menuLinks" :key="link"
                        class="col-12 text-center list-group-item text-capitalize"><a class=" text-decoration-none"
                            :href="link.url">{{ link.text }}</a></li>
                    <li class="list-group-item col-12 d-flex flex-row justify-content-center text-capitalize"><a
                            class="btn fs-5 btn-primary text-light fw-bold text-decoration-none"
                            href="#/seja-membro">seja membro</a></li>
                    <li class="list-group-item col-12 d-flex flex-row justify-content-center text-capitalize"><a
                            class="btn fs-5 btn-light text-primary fw-bold text-decoration-none"
                            href="#/login">login</a></li>


                </ul>
            </nav>
        </div>
    </header>
</template>