<script>
import { defineComponent, onBeforeMount,defineAsyncComponent, ref } from 'vue';
import MainBannerComponent from '../components/MainBannerComponent.vue';

//import PostCardComponent from '../components/PostCardComponent.vue';
import TelegramIcon from '../assets/icons/TelegramIcon.vue';
import env from '../../env.json';

const PostCardComponent = defineAsyncComponent(() =>
  import('../components/PostCardComponent.vue')
)



export default defineComponent({
    components: { MainBannerComponent, PostCardComponent, TelegramIcon },
    setup() {
        const treinamentos = ref([]);
        const pagePosts = ref(0);
        const pageTreinamentos = ref(0);
        var posts = ref([]);





        const updateTreinamentos = async (url, delay) => {
            setInterval(async () => {
                try {
                    let tmp = await fetch(url).then(res => res.json()).then(resp => resp)
                    let incluir = !(treinamentos.value.map((e) => {
                        return e.id
                    })).includes(tmp[0].id)

                    if (incluir) {
                        treinamentos.value = tmp.concat(treinamentos.value)
                        treinamentos.value = treinamentos.value.sort((a, b) => {
                            const dateA = new Date(a.date);
                            const dateB = new Date(b.date);
                            return dateB - dateA;
                        })


                    }
                } catch (error) {
                    if (env.DEBBUGING)
                        console.error('json returns invalid content', error)
                }
            }, 1000 * delay)
        }


        const updatePosts = (url, delay) => {
            setInterval(async () => {
                try {
                    let tmp = await fetch(url).then(res => res.json()).then(resp => resp)



                    let incluir = !(treinamentos.value.map((e) => {
                        return e.id
                    })).includes(tmp[0].id)

                    if (incluir) {
                        treinamentos.value = tmp.concat(treinamentos.value)
                        treinamentos.value = treinamentos.value.sort((a, b) => {
                            const dateA = new Date(a.date);
                            const dateB = new Date(b.date);
                            return dateB - dateA;
                        })


                    }
                } catch (error) {
                    console.error('json returns invalid content', error)
                }
            }, 1000 * delay)
        }


        const getOldTreinamentos = async () => {
            try {
                pageTreinamentos.value++
		console.log(pageTreinamentos.value)
                let url = env.API_URL+'?treinamentos&page=' + pageTreinamentos.value;
                //console.log(treinamentos.value)

                let tmp = await fetch(url).then(res => res.json()).then(resp => resp)
                treinamentos.value = treinamentos.value.concat(tmp)
                treinamentos.value = treinamentos.value.sort((a, b) => {
                    const dateA = new Date(a.date);
                    const dateB = new Date(b.date);
                    return dateB - dateA;
                })
            }
            catch (error) {
                console.error('json returns invalid content', error)
            }

        }


        const getOldPosts = async () => {
            try {
                pagePosts.value++
                let url = env.API_URL+'?posts&page=' + pagePosts.value



                let tmp = await fetch(url).then(res => res.json()).then(resp => resp)
                posts.value = posts.value.concat(tmp)

                posts.value = posts.value.sort((a, b) => {
                    const dateA = new Date(a.date);
                    const dateB = new Date(b.date);
                    return dateB - dateA;
                })

            }
            catch (error) {
                console.error('json returns invalid content', error)
            }
        }







        onBeforeMount(async () => {

            /* treinamentos.value = await fetch(env.API_URL+'?treinamentos').then(res => res.json()).then(resp => resp)
            treinamentos.value = treinamentos.value.sort((a, b) => {
                const dateA = new Date(a.date);
                const dateB = new Date(b.date);
                return dateB - dateA;
            })


            posts.value = await fetch(env.API_URL+'?treinamentos').then(res => res.json()).then(resp => resp)
            posts.value = treinamentos.value.sort((a, b) => {
                const dateA = new Date(a.date);
                const dateB = new Date(b.date);
                return dateB - dateA;
            }) */

            getOldTreinamentos(env.API_URL+'?treinamentos');
            getOldPosts(env.API_URL+'?posts');

            updateTreinamentos(env.API_URL+'?treinamentos&q=1', 6);
            updatePosts(env.API_URL+'?posts&q1', 6);



        })


        return {
            treinamentos,
            posts,
            getOldPosts,
            getOldTreinamentos
        }
    }
})
</script>

<template>
    <div class="d-flex flex-column  align-items-center">
        <div class="col-9">
            <MainBannerComponent v-once />
        </div>
        <div class="col-8">

            <section v-if="treinamentos.length == 0 && posts.length == 0">
                <div class="col-12 d-flex flex-row flex-wrap justify-content-start">
                    <div >
                        <div class="col-sm-12 px-3 my-3">
                            <h1 class="text-center">
                                Nenhuma postagem relacionada foi encontrada!
                            </h1>
                        </div>
                        <div class="col-12 mt-5 d-flex flex-row justify-content-center">
                            <button @click="getOldTreinamentos()" type="button" class="btn btn-primary">Tentar
                                Novamente</button>
                        </div>
                    </div>

                </div>

            </section>
            <div v-else>
                <section>
                <div
                    class="d-flex flex-sm-row flex-column align-items-center  mb-4 justify-content-between align-items-end">
                    <h2 class="m-0 fw-bold text-capitalize">treinamentos</h2>


                    <TelegramIcon />


                </div>
                <div class="col-12">
                    <div v-if="treinamentos.length > 0" class="col-12 d-flex flex-row flex-wrap justify-content-start">
                        <div class="col-sm-4 px-3 my-3" v-for="post in treinamentos" :key="post">
                            <PostCardComponent  style="max-width:380px;" :post="post" />
                        </div>
                        <div class="col-12 mt-5 d-flex flex-row justify-content-center">
                            <button @click="getOldTreinamentos()" type="button" class="btn btn-primary">ver
                                mais</button>
                        </div>
                    </div>
                    <div v-else>
                        <div class="col-sm-12 px-3 my-3">
                            <h1 class="text-center">
                                Nenhuma postagem relacionada foi encontrada!
                            </h1>
                        </div>
                        <div class="col-12 mt-5 d-flex flex-row justify-content-center">
                            <button @click="getOldTreinamentos()" type="button" class="btn btn-primary">Tentar
                                Novamente</button>
                        </div>
                    </div>

                </div>

            </section>

            <section>
                <div class="d-flex col-12 d-flex flex-sm-row flex-column justify-content-between">
                    <div class=" ">
                        <h2 class="m-0 fw-bold text-capitalize col-10">blog</h2>
                    </div>

                    <div class="col-sm-6 col-12 mt-sm-3">
                        <div class="input-group d-flex flex-row justify-content-center col-6">
                            <span style="border-right:none !important;" class="btn border bg-transparent" type="button"
                                id="botao-pesquisar">
                                <i class="bi text-secondary bi-search"></i>
                            </span>
                            <input style="border-left:none !important;" type="text" class="form-control "
                                placeholder="Pesquisar..." aria-label="Pesquisar" aria-describedby="botao-pesquisar">

                        </div>
                    </div>

                </div>
                <div class="col-12">



                    <div v-if="posts.length > 0" class="col-12 d-flex flex-row flex-wrap justify-content-start">
                        <div class="col-sm-4 px-3 my-3" v-for="post in posts" :key="post">
                            <PostCardComponent style="max-width:380px;" :post="post" />
                        </div>
                        <div class="col-12 mt-5 d-flex flex-row justify-content-center">
                            <button @click="getOldPosts()" type="button" class="btn btn-primary">ver
                                mais</button>
                        </div>
                    </div>
                    <div v-else>
                        <div class="col-sm-12 px-3 my-3">
                            <h1 class="text-center">
                                Nenhuma postagem relacionada foi encontrada!
                            </h1>
                        </div>
                        <div class="col-12 mt-5 d-flex flex-row justify-content-center">
                            <button @click="getOldPosts()" type="button" class="btn btn-primary">Tentar
                                Novamente</button>
                        </div>
                    </div>
                </div>
            </section>
            </div>
        </div>
    </div>
</template>

<style scoped>
section {
    margin-top: 10vw;
}
</style>
