<script>
import { defineComponent, ref, onMounted } from "vue";
import MarkdownIt from "markdown-it";
import { usePostsStore } from "@/stores/posts";
import CTAComponent from "../components/CTAComponent.vue";

export default defineComponent({
	setup(props) {
		let markdownContent = ref("");
		let post = usePostsStore();
		const loadMarkdownFile = async () => {
			try {
				let url = window.location.href.split("#")[1].split("p=");
				const response = await fetch(url[0].split("?")[0] + "/" + url[1]);
				if (!response.ok) {
					throw new Error("Erro ao carregar o arquivo Markdown");
				}
				const markdownText = await response.text();
				const md = new MarkdownIt();
				markdownContent.value = md.render(markdownText);
				console.log(post.post);
			} catch (error) {
				console.error("Erro:", error);
			}
		};
		let verMais = ref(true);
		let showCTA = ref(false);
		let callCTA = () => {
			let auth = false;
			if (auth) {
				null;
			} else {
				verMais.value = false;
			}
		};

		onMounted(() => {
			loadMarkdownFile();
			setTimeout(() => {
				let md = document.getElementById("md");
				Array.from(md.children).forEach((e, i) => {
					if (i > Array.from(md.children).length / 2) console.log(e.remove());
				});

				md.appendChild(verMais);
			}, 1000);
		});
		return {
			props,
			showCTA,
			markdownContent,
			post,
		};
	},
	components: { CTAComponent },
});
</script>

<template>
	<div class="d-flex flex-sm-row justify-content-center">
		<div class="col-sm-8 col-12 px-3 px-sm-0">
			<div class="col-12" style="position: relative">
				<img :src="post.post.img" alt="" class="col-12" />
				<div
					class="col-12 text-center d-flex flex-column justify-content-center"
					style="
						position: absolute;
						height: 100%;
						top: 0;
						left: 0;
						width: 100%;
						background-color: rgba(0, 0, 0, 0.4);
					"
				>
					<span class="text-light fs-1 fw-bold">TESTE</span>
				</div>
			</div>

			<div id="md" v-html="markdownContent"></div>
			<Teleport to="#modal" v-if="showCTA">
				<div
					class="d-flex flex-row modal justify-content-center col-12 px-5 align-items-center bg-dark bg-opacity-50"
				>
					<div class="col-6"><CTAComponent /></div>
				</div>
			</Teleport>
		</div>
	</div>
</template>
